import React, { useState } from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"

import { useStaticQuery, graphql } from "gatsby"
import { ParallaxProvider } from "react-scroll-parallax"
import ParallaxBanner from "../components/parallaxBanner"

import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import Button from '@material-ui/core/Button';
import { TextareaAutosize } from "@material-ui/core";




const Offertforfragan = () => {
    const { gcms } = useStaticQuery(graphql`
    {
        gcms {
            offertforfragans {
                hero {
                  url
                }
              }
        }
    }`)
    const [service, setService] = useState(null)
    const [message, setMessage] = useState("")
    return (<Layout>
        <SEO title="Offertförfrågan" />
        <ParallaxProvider>
            <div class="cntnr column-flex">
                <ParallaxBanner url={gcms.offertforfragans[0].hero.url} heading="Offertförfrågan" backgroundPosition="right center"/>
                <section class="contact-section">
                    <h1 class="section-header">Offertförfrågan</h1><br />
                    <div class="contact-container">
                <h2 class="section-header">Kontakta oss</h2>
                <form id="offert-form" style={{ }} action="https://getform.io/f/9ab82b0b-c75d-4d5b-8382-2d3e1a6aef68" method="POST">
                    <FormControl >

                        <InputLabel htmlFor="name-input">Ditt namn</InputLabel>
                        <Input  type="text" name="name" />



                    </FormControl>
                    <FormControl >

                        <InputLabel htmlFor="email-input">Din email</InputLabel>
                        <Input type="email"  name="email" />



                    </FormControl>
                    <FormControl >

                        <InputLabel htmlFor="tel-input">Ditt telefonnummer</InputLabel>
                        <Input type="tel"  name="tel" />



                    </FormControl>

                    <FormControl >
                        <InputLabel id="services-select-label">Välj tjänst</InputLabel>
                        <Select
                            labelId="services-select-label"
                            id="services-select"
                            onChange={(event) => { setService(event.target.value)}}
                        >
                            <MenuItem value={"Golvläggning"}>Golvläggning</MenuItem>
                            <MenuItem value={"Golvslipning"}>Golvslipning</MenuItem>
                            <MenuItem value={"Mattläggning"}>Mattläggning</MenuItem>
                            <MenuItem value={"Golvavjämning"}>Golvavjämning</MenuItem>
                            <MenuItem value={"Vattenskador"}>Vattenskador</MenuItem>
                            <MenuItem value={"Trapprenovering"}>Trapprenovering</MenuItem>

                        </Select>
                    </FormControl>
                    <FormControl class="form-textarea">
                        <TextareaAutosize onChange={(event) => { setMessage(event.target.value) }} aria-label="beskriv ärende" rowsMin={5} placeholder="Beskriv ditt ärende" />
                    </FormControl>
                    <FormControl>
                        <input hidden  type="text" name="message" value={`Intresserad av tjänsten: ${service} \n Meddelande:\n ${message}`} />
                    </FormControl>
                    <Button type="submit" variant="contained">Skicka</Button>
                </form>
            </div>
                </section>
            </div>
        </ParallaxProvider>
    </Layout>
    )
}

export default Offertforfragan;